@import "../../../Common/Styles/SASS/Variables";
@import "../../../Common/Styles/Mixins/Mixins";

.skill {
    width: 150px;
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
    border: 1px solid $lineColor;
    margin-right: 50px;
    margin-bottom: 30px;

    .skill:not(:last-child) {
        margin-right: 30px;
    }
    @media (max-width: 700px) {
        width: 90px;
        height: 90px;
        padding: 5px;
        margin-right: 20px;
        margin-bottom: 10px;
    }

    .icon {
        height: 60px;
        width: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;
        margin-bottom: 10px;
        color: $textDescriptionColor;

        @media (max-width: 700px) {
            width: 40px;
            height: 40px;
            margin-bottom: 5px;
            margin-top: 10px;
        }
    }

    h3 {
        @include text($font, 16px, 1.2, 500);
        color: $textTitleColor;
        margin: 0 0 5px;

        @media (max-width: 700px) {
            font-size: 12px;
        }
    }
}


